import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        // 搜索词
        search: '',
        // 商品
        products: []
        // products: [
        //     {
        //         "img": "/static/img/list/05.jpg",
        //         "title": "黑森林1个，约8英寸，圆形",
        //         "sales": 44,
        //         "price": 87,
        //         "originPrice": 128,
        //         "evaluate": 4.5,
        //         "_id": "5"
        //     },
        //     {
        //         "img": "/static/img/list/06.jpg",
        //         "title": "单人餐，包间免费",
        //         "sales": 101,
        //         "price": 13,
        //         "originPrice": 30,
        //         "evaluate": 3.8,
        //         "_id": "2"
        //     },
        //     {
        //         "img": "/static/img/list/01.jpg",
        //         "title": "四人餐，提供免费WiFi",
        //         "sales": 241,
        //         "price": 89,
        //         "originPrice": 105,
        //         "evaluate": 4.2,
        //         "_id": "3"
        //     },
        //     {
        //         "img": "/static/img/list/02.jpg",
        //         "title": "晚餐自助，提供免费WiFi",
        //         "sales": 13126,
        //         "price": 45.5,
        //         "originPrice": 60,
        //         "evaluate": 3.9,
        //         "_id": "4"
        //     }
        // ]
    },
    // 计算属性数据
    getters: {
        // 计算商品总价
        price(state) {
            // 计算products商品总价
            return state.products
                // 求和
                // .reduce((res, item) => {
                //     return res + parseInt(item.price)
                // }, 0)
                // 省略
                .reduce((res, item) => res + parseInt(item.price), 0)
                // 保留两位小数
                .toFixed(2);
        }
    },
    mutations: {
        // 同步消息
        // 接收搜索内容
        search(state, value) {
            // console.log(value)
            // 存储数据
            state.search = value;
        },
        // 添加商品消息
        addItem(state, data) {
            // console.log('addItem')
            // 如果商品没有价格，不能提交
            if (Object.is(NaN, +data.price)) {
                return alert('商品没有价格，不能加入购物车')
            }
            // 加入购物车
            state.products.push(data)
        },
        // 移除商品的消息
        deleteItem(state, data) {
            // console.log('deleteItem')
            // 获取商品在购物车中的位置
            let index = state.products.findIndex(item => item._id === data._id);
            // 如果存在
            if (index >= 0) {
                // 移除商品
                state.products.splice(index, 1)
            } else {
                // 提示用户
                alert('该商品不在购物车中，不能移除！');
            }
        }
    },
    actions: {

    }
})
