<template>
<div class="ickt-header">
    <div class="left" @click="clickLeft"></div>
    <div class="title">
        <!-- 传递了title显示titile，没有传递，显示slot -->
        <h1 v-if="title">{{title}}</h1>
        <slot v-else></slot>
    </div>
    <div class="right">
        <slot name="right"></slot>
    </div>
</div>
</template>
<style scoped lang="scss">
// 引入配置
@import '@/base.scss';
.ickt-header {
    display: flex;
    height: 50px;
    line-height: 50px;
    background-color: $color;
    color: #fff;
    text-align: center;
    a {
        color: #fff;
        text-decoration: none;
    }
    .left,
    .right {
        width: 60px;
    }
    .title {
        flex: 1;
        text-indent: -0.5em;
        h1 {
            font-size: 24px;
            font-weight: normal;
        }
    }
    .left {
        position: relative;
        &::before,
        &::after {
            @include arrow(8px, #fff, right);
            content: "";
            position: absolute;
            top: 17px;
            left: 10px;
        }
        &:after {
            border-right-color: $color;
            left: 13px;
        }
    }
}
</style>
<script>
export default {
    // 接收数据
    props: ['title', 'clickLeft']
}
</script>